import React from "react";
import { getNamesByTagSlug } from "../../../../../utils";
import { TagIconSolid as TagIcon } from "../../../../common";
import { TagContainer } from "./container";
import { TagLink } from "./link";
import { TagWrapper } from "./wrapper";

export const TagInfos = ({ tags }) => {
  return (
    <TagWrapper>
      <TagIcon size={23} />
      {tags?.map((tag, idx) => (
        <TagContainer key={idx}>
          <TagLink to={`/tag/${tag}`}>{getNamesByTagSlug(tag).ko}</TagLink>
        </TagContainer>
      ))}
    </TagWrapper>
  );
};
