import styled from "styled-components";
import { baseStyles } from "../../../../styles";

export const Button = styled.button`
  all: unset;
  ${baseStyles()}

  position: relative;
  width: 100%;
  padding: 10px;

  border-left: ${({ level, theme }) =>
    level > 0 ? `2px solid ${theme.colors.grayBorder}` : ``};

  cursor: pointer;

  color: ${({ theme }) => theme.colors.textColor};

  background-color: ${({ isCurrentPage, theme }) =>
    isCurrentPage
      ? theme.colors.minimumHighlight
      : theme.colors.backgroundColor};

  &:hover {
    background-color: ${({ theme }) => theme.colors.minimumHighlight};
    border-left: ${({ level, theme }) =>
      level > 0 ? `2px solid ${theme.colors.minimumHighlight}` : ``};

    & > div {
      background: ${({ theme }) =>
        "linear-gradient(to right bottom, " +
        theme.colors.highlightColor +
        " 50%, " +
        theme.colors.backgroundColor +
        " 50%);"};

      border-top: 1px solid ${({ theme }) => theme.colors.grayBorder};
      border-left: 1px solid ${({ theme }) => theme.colors.grayBorder};
    }
  }
`;
